/**
 * Slider specific styles
 */

.slider {
    position: relative;
    // width: 320px;
    margin: 0 auto;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: -moz-none;
    -o-user-select: none;
    user-select: none;
    padding: 0;
}

.frame,
.events_log {
    position: relative;
    // width: 880px;
    width: 87.5%;
    margin: 0 auto;
    font-size: 0;
    line-height: 0;
    overflow: hidden;
    white-space: nowrap;
}

.frame li {
    position: relative;
    display: inline-block;
    text-align: center;
    white-space: normal;
    vertical-align: top;
}

.slides {
    display: inline-block;
    padding: 0;

    li {
    position: relative;
    display: inline-block;
    max-width: 100%;
  }
}

.simple > .frame li,
.rewind > .frame li,
.events > .frame li {
    width: 270px;
    margin-right: 10px;
}

.dots {
    margin: 0;
    padding: 0;
    text-align: center;
    position: absolute;
    width: 100%;
}

.dots > li {
    background-color: #eee;
    border: 1px solid #666;
    border-radius: 5px;
    box-shadow: inset 1px 1px 1px #888;
    display: inline-block;
    height: 10px;
    width: 10px;
    margin: 0 5px;
    cursor: pointer;
}

.dots > li.active {
    background-color: $link-color; // MAKE IT BLUE CAUSE ACTIVE!
    box-shadow: inset 2px 0px 2px -2px #333; // Change the boxshadow inset... just cause
}

.percentage .slides {
    display: block;
    padding: 0;
}

.percentage .frame {
    width: 87.452%;
}

.percentage li {
    width: 100%;
}

.variablewidth li {
  max-width: 200px;
  width: auto;
  margin-right: 50px;
  display: inline-block;
  vertical-align: middle;
}

.multislides .slides {
  display: flex;
}

.multipleelements li, .multislides li, .ease li {
  display: flex;
  flex-direction: column;
  flex-basis: 33.33%;
  max-width: 33.33%;
  box-sizing: border-box;
  flex: 0 0 auto;
  padding-right: .5rem;
  padding-left: .5rem;
  margin-right: $spacer-xs;
}

.multislides li p {
  flex-grow: 1;
}

.simple > .frame li:last-child,
.rewind > .frame li:last-child,
.events > .frame li:last-child,
.variablewidth li:last-child,
.multipleelements li:last-child,
.multislides li:last-child,
.ease li:last-child {
    margin-right: 0;
}

.prev, .next {
    position: absolute;
    top: 50%;
    margin-top: -25px;
    display: block;
    cursor: pointer;
}

.next {
    right: 0;
}

.prev {
    left: 0;
}

.next.disabled,
.prev.disabled {
    opacity: .3;
    pointer-events: none;
}

.next svg, .prev svg {
    width: 25px;
    transition: .4s;
}

.next svg:hover, .prev svg:hover {
    transform: scale(1.1);
}

.events_log {
    display: block;
    font-size: 12px;
    line-height: 1.6;
    text-align: center;
    background-color: #fcfcfc;
    border: 1px solid #d2d2d2;
    margin-top: -20px;
    margin-bottom: 20px;
}

.events_log > span {
    display: none;
}

.events_log > span:nth-last-child(-n+5) {
    display: block;
}

.slider {
  &.small-logo {
    img {
      max-height: 38px;
      max-width: 140px;
    }
  }
}
