#legal {
	ol {
	  list-style-type: none;
	  counter-reset: item;
	  margin: 0;
	  padding: 0;
	}

	li {
	    font-size: 18px;
	    font-weight: 300;
	    line-height: 1.775;
	}

	ol > li {
	  display: table;
	  counter-increment: item;
	  margin-bottom: 0.6em;
	}

	ol > li:before {
	  content: counters(item, ".") ". ";
	  display: table-cell;
	  padding-right: 0.6em;    
	}

	li ol > li {
	  margin: 0.5rem 0;
	}

	li ol > li:before {
	  content: counters(item, ".") " ";
	}
}